@import url('https://fonts.googleapis.com/css2?family=Cinzel&family=Fauna+One&display=swap');


* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.App {
  margin: auto;
}

.btn{
  display: inline-block;
  border: none;
  background: #333;
  color: #f3f3f3;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover{
  background: #555;
}

.container {
  padding: 1rem 2rem;
}